*{font-family:"Monda", sans-serif;margin:0;padding:0;box-sizing:border-box;font-weight:300}h1,h2,h2{color:#363434;font-weight:400}h3{color:#646464;font-weight:400}.App{display:-webkit-flex;display:flex;-webkit-flex-direction:row-reverse;flex-direction:row-reverse;transition:all 0.5s ease}.All{width:100vw}.library-active{margin-left:30%}.song-container{min-height:60vh;background:white;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.song-container img{width:20%;border-radius:50%}.song-container h2{padding:3rem 1rem 1rem 1rem}.song-container h3{font-size:1rem}@media screen and (max-width: 1200px){.song-container img{width:35%}}@media screen and (max-width: 992px){.song-container img{width:45%}}@media screen and (max-width: 768px){.song-container img{width:60%}}body{padding:0;margin:0}.player{min-height:20vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.time-control{width:50%;background:white;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.time-control input{width:100%;-webkit-appearance:none;background:transparent;cursor:pointer}.play-control{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;width:40%;padding:1rem}.play-control svg{cursor:pointer}.volume{display:-webkit-flex;display:flex;width:40%;-webkit-justify-content:center;justify-content:center;grid-gap:10px;gap:10px}.volume .volumeRange{width:10vw}input[type="range"]:focus{outline:none}input[type="range"]::-webkit-slider-thumb{-webkit-appearance:none;height:16px;width:16px}.volumeChangeInput::-moz-range-thumb{-webkit-appearance:none;background:transparent;border:none;height:16px;width:16px}.track{background-color:lightblue;width:100%;height:1rem;position:relative;border-radius:1rem;overflow:hidden;margin:1rem}.animate-track{background:#ccc;width:100%;height:100%;position:absolute;top:0;left:0;-webkit-transform:translateX(0%);transform:translateX(0%);pointer-events:none}@media screen and (max-width: 768px){.time-control{width:80%}.play-control{width:70%}}*{scrollbar-width:thin;scrollbar-color:rgba(155,155,155,0.5) transparent}* ::-webkit-scrollbar{width:5px}* ::-webkit-scrollbar-track{background:transparent}* ::-webkit-scrollbar-thumb{background-color:rgba(155,155,155,0.5);border-radius:20px;border:transparent}.library{-webkit-transform:translateX(-100%);transform:translateX(-100%);transition:all 0.5s ease;opacity:0;position:fixed;top:0;left:0;width:20rem;height:100%;background:white;box-shadow:2px 2px 50px #bebcbc;overflow:scroll}.library .libraryTheName{padding:1rem}.library-song{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:1rem 2rem 1rem 2rem;cursor:pointer;transition:background 0.25s ease}.library-song img{width:30%}.library-song:hover{background:#b4e1e7}.library-song .song-description{padding-left:1rem}.library-song .song-description h3{font-size:1rem}.library-song .song-description h4{font-size:0.7rem}.selected{background:#b4e1e7}.active-library-display{display:block}.active-library{-webkit-transform:translateX(0%);transform:translateX(0%);opacity:1}@media screen and (max-width: 768px){.library{width:100%}}nav{min-height:10vh;display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center}nav button{background-color:transparent;font-size:0.95rem;border:2px solid #383838;border-radius:7%;padding:2px 5px;cursor:pointer;transition:all 0.3s ease-in-out}nav button:hover{background:#414141;color:white}@media screen and (max-width: 768px){nav button{z-index:10}}

